<template>
  <div class="adminuser-list">
    <div class="adminuser-list__header">
      <el-input
        v-model.trim="listQuery.search"
        placeholder="请输入成员姓名"
        style="width: 220px;"
        size="small"
        @keyup.enter.native="handleFilter"
      />
      <el-button
        size="small"
        style="margin-left: 30px;"
        type="primary"
        @click="handleFilter"
      >
        搜索
      </el-button>
      <el-button
        size="small"
        style="margin-left: 10px;"
        type="primary"
        plain
        @click="handleReset"
      >
        重置
      </el-button>
    </div>
    <div class="adminuser-list__body">
      <el-card class="el-card--no-shadow el-card--header-nbb el-card--flex">
        <div slot="header" class="title">成员列表</div>
        <el-table
          :header-row-style="getHeadStyle"
          :data="tableData"
          empty-text="暂无数据，请尝试输入其他关键字"
          style="margin-top:30px;width: 100%;"
        >
          <el-table-column
            align="center"
            fixed
            prop="name"
            label="成员姓名"
            width="120"
            style="text-align: center;"
          />
          <el-table-column
            align="center"
            label="成员性别"
            prop="genderName"
            min-width="100"
          />
          <el-table-column
            align="center"
            prop="id"
            label="成员ID"
            min-width="120"
          />
          <el-table-column
            align="center"
            prop="userid"
            label="钉钉ID"
            min-width="190"
          />
          <el-table-column
            align="center"
            prop="department"
            label="部门"
            min-width="190"
          />
          <el-table-column
            align="center"
            prop="roles"
            label="权限组"
            min-width="190"
          >
            <template slot-scope="{ row }">
              {{ row.roles && row.roles.name }}
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            prop="mobile"
            label="电话"
            min-width="190"
          />
          <el-table-column
            align="center"
            prop="email"
            label="邮箱"
            min-width="190"
          />
          <el-table-column
            align="center"
            fixed="right"
            label="操作"
            width="190"
          >
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="handleClickEdit(scope.row)"
              >
                编辑</el-button
              >
              <el-button
                type="text"
                size="small"
                @click="handlerClickConLog(scope.row)"
                >查看日志</el-button
              >
            </template>
          </el-table-column>
          <template slot="empty">
            <div class="table-empty">
              暂无数据，请尝试输入其他关键字
            </div>
          </template>
        </el-table>
        <div class="adminuser-list__body__pagination">
          <el-pagination
            :current-page.sync="listQuery.currentPage"
            :page-sizes="[10, 50, 100]"
            :page-size="listQuery.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="tableCount"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          />
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import { getList } from '@/api/adminUser'
import Vue from 'vue'
const genderFilter = Vue.filter('gender')
export default {
  name: 'AdminUserManager',
  data() {
    return {
      listQuery: {
        search: '',
        pageSize: 10,
        currentPage: 1
      },
      tableCount: 0,
      tableData: []
    }
  },

  mounted() {
    this.handlerGetList(this.listQuery)
  },
  methods: {
    genderFilter,
    handlerGetList(query) {
      getList(query).then(result => {
        this.tableData = result.data.list
        for (let i = 0; i < this.tableData.length; i++) {
          this.tableData[i].genderName = this.tableData[i].gender
            ? genderFilter(this.tableData[i].gender)
            : '未选择'
        }
        this.tableCount = result.data.count
      })
    },
    handleSizeChange(e) {
      this.listQuery.pageSize = e
      this.handlerGetList(this.listQuery)
    },
    handleCurrentChange(e) {
      this.listQuery.currentPage = e
      this.handlerGetList(this.listQuery)
    },
    handleFilter() {
      this.listQuery.currentPage = 1
      if (!this.listQuery.search) {
        this.$message({
          message: '请输入成员姓名',
          type: 'error'
        })
        return
      }
      this.handlerGetList(this.listQuery)
    },

    handleReset() {
      this.listQuery = {
        search: '',
        pageSize: 10,
        currentPage: 1
      }
      this.handlerGetList(this.listQuery)
    },
    getHeadStyle() {
      return {
        fontSize: '14px',
        color: '#17233D',
        letterSpacing: 0
      }
    },
    handlerClickConLog(row) {
      this.$router.push({
        path: `/system-settings/user/${
          row._id
        }/conlog?name=${encodeURIComponent(row.name)}`
      })
    },
    handleClickEdit(row) {
      this.$router.push({
        path: `/system-settings/user/${row._id}/edit?name=${encodeURIComponent(
          row.name
        )}`
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.adminuser-list {
  min-height: 100%;
  &__body {
    padding-top: 30px;
    &__pagination {
      padding: 60px 0 40px;
      text-align: center;
    }
  }
}
</style>
